.container {
    display: flex;
}

.container_row {
    flex-direction: row;
}

.container_column {
    flex-direction: column;
}

.container_justify-start {
    justify-content: flex-start;
}

.container_justify-center {
    justify-content: center;
}

.container_justify-end {
    justify-content: flex-end;
}

.container_justify-between {
    justify-content: space-between;
}

.container_align-start {
    align-items: flex-start;
}

.container_align-center {
    align-items: center;    
}

.container_align-end {
    align-items: flex-end;
}



